"use client"
import React, { useState } from 'react'
import style from "./style.module.css"
import '../globals.css'
import FirstStep from './components/FirstStep';

function page() {

  const [activeStep, setActiveStep] = useState(0);

  const handleNext = () => {
    setActiveStep((cur) => cur + 1)
  }

  const steps = [
    {
      step: 1,
      about: "Course Information",
      ele: <FirstStep handleNext={handleNext} />
    },
    {
      step: 2,
      about: "Course Option",
      // ele: <SecoundStep handleNext={handleNext} />
    },
    {
      step: 3,
      about: "Course Content",
      // ele: <FirstStep handleNext={handleNext} />
    },
    {
      step: 4,
      about: "Course Preview",
      // ele: <FirstStep handleNext={handleNext} />
    }
  ];

  return (
    <main>
      <div className={`${style.bgLeftSide}  p-5  h-screen w-[500px]`}>
        <span className='text-white font-medium mt-20 block'>Tell us about your company</span>
      </div>
      <div className=' p-5 w-[75%] bg-white h-[800px] shadow-2xl rounded-xl absolute top-1/2 bottom-1/2 left-1/2 right-1/2 -translate-x-1/2 -translate-y-1/2 '>
        <h1 className='text-center'>Creat a shipping company account</h1>
        <div className='flex items-center'>
          <div>
            {steps.map((item, index) => (
              <div className='flex items-center' key={index}>
                <div className={`cursor-pointer transition-all duration-300 font-medium text-lg my-5 p-1.5 px-4 rounded-full shadow-md shadow-[#00000040] ${activeStep >= index
                  ? 'bg-[#3E5AF0] text-white'
                  : 'bg-[#F9F9F9] text-black'
                  }`}
                  onClick={() => setActiveStep(index)}
                >
                  {item.step}
                </div>
                {activeStep >= index && (
                  <span className='ms-3 text-[#526484]'>{item.about}</span>
                )}
              </div>
            ))}
          </div>

          <div className='shadow-md shadow-[#00000040] h-full bg-white p-3 '>
            {steps[activeStep].ele}

          </div>
        </div>
      </div>
    </main>
  )
}

export default page
